import {mapState} from 'vuex';
export default{
    data() {
        return {
            name: '',
            phoneNum: '',
            idCard: '',
            bankCardNum: '',
            verificationCode: ''
        };
    },
    computed: {
        ...mapState(['global'])
    },
    watch: {

    },
    methods: {
        viewProtocol() {
            this.$router.push({path: `/agreementInfo`});
        },
        getSmsSeqNo () {
            if (this.name === '') {
                this.$vux.toast.text('姓名不能为空', 'bottom');
                return;
            }
            if (this.phoneNum === '' || !(/^1[3456789]\d{9}$/.test(this.phoneNum))) {
                this.$vux.toast.text('电话号码格式错误', 'bottom');
                return;
            }
            if (this.idCard === '' || (!(/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$/).test(this.idCard) && !(/^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{4}$/.test(this.idCard)))) {
                this.$vux.toast.text('身份证号码格式错误', 'bottom');
                return;
            }
            if (this.bankCardNum === '') {
                this.$vux.toast.text('银行卡号不能为空', 'bottom');
                return;
            }
            this.axios({
                method: 'post',
                url: '/Api/GuiPayByqiao',
                data: '{"id":19,"jsonrpc":"2.0","method":"applySign","params":{"realName":"' + this.name + '", "mobileBank":"' + this.phoneNum + '", "idCard":"' + this.idCard + '", "BkAcctNo":"' + this.bankCardNum + '"}}',
                contentType: 'application/json',
                dataType: 'json'
            }).then((result) => {
                console.log(result);
            });
        },
        bindCard() {
            if (this.name === '') {
                this.$vux.toast.text('姓名不能为空', 'bottom');
                return;
            }
            if (this.phoneNum === '' || !(/^1[3456789]\d{9}$/.test(this.phoneNum))) {
                this.$vux.toast.text('电话号码格式错误', 'bottom');
                return;
            }
            if (this.idCard === '' || (!(/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$/).test(this.idCard) && !(/^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{4}$/.test(this.idCard)))) {
                this.$vux.toast.text('身份证号码格式错误', 'bottom');
                return;
            }
            if (this.bankCardNum === '') {
                this.$vux.toast.text('银行卡号不能为空', 'bottom');
                return;
            }
            if (this.verificationCode === '') {
                this.$vux.toast.text('验证码不能为空', 'bottom');
                return;
            }

            this.axios({
                method: 'post',
                url: '/Api/GuiPayByqiao',
                data: '{"id":19,"jsonrpc":"2.0","method":"confirmSign","params":{"realName":"' + this.name + '", "mobileBank":"' + this.phoneNum + '", "idCard":"' + this.idCard + '", "BkAcctNo":"' + this.bankCardNum + '", "msgCode":"' + this.verificationCode + '"}}',
                contentType: 'application/json',
                dataType: 'json'
            }).then((result) => {
                console.log(result);
                if (result.data.result.code === 50000) {
                    this.$router.push({path: `/cardInfo/${result.data.result.SgnNo}`});
                } else {
                    this.$vux.toast.text('请求失败，请检查数据格式', 'bottom');
                }
            });
        }
    },
    created () {
    }
};



